import { SwiperSlide } from 'swiper/react';
import { GloabelProductCard } from '../../components';

export const renderSwiperProdcuts = ['p-1', 'p-3', 'p-2', 'p-3', 'p-1', 'p-2'].map((product, index) => {
    return <SwiperSlide>
        <GloabelProductCard key={index} hasDiscount={index % 2 === 0} hasOffer={index % 2 !== 0} classNames={'col-span-12 sm:col-span-6 lg:col-span-4'} imageName={product}></GloabelProductCard>
    </SwiperSlide>
});
