import { Fragment } from 'react';
import { Rating } from '../../../../components';
const categories = [
    { name: 'الكل', key: 1, count: 12 },
    { name: 'الإلكترونيات والموبايلات', key: 2, count: 15 },
    { name: 'التلفزيونات والشاشات', key: 3, count: 33 },
    { name: 'سماعات أذن', key: 4, count: 43 },
    { name: 'كاميرات ديجتال', key: 5, count: 123 },
    { name: 'مكبرات صوت', key: 6, count: 53 },
];
const brands = [
    { name: 'الكل', key: 7, count: 100 },
    { name: 'إتش بي', key: 8, count: 16 },
    { name: 'أبل', key: 9, count: 12 },
    { name: 'سامسونج', key: 10, count: 11 },
    { name: 'ديل', key: 11, count: 8 },
    { name: 'لينوفو', key: 12, count: 6 },
];
const colors = [
    { name: 'الكل', key: 13, count: 100 },
    { name: "احمر", key: 14, color: "#F55157", count: 9 },
    { name: "اخضر", key: 15, color: "#00AF6C", count: 7 },
    { name: "اسود", key: 16, color: "#333333", count: 2 },
    { name: "أزرق", key: 17, color: "#62D0B6", count: 4 },
    { name: "أصفر", key: 17, color: "#FFAF44", count: 8 },
];
const ratings = [
    { name: 'الكل', key: 18, count: 100 },
    { key: 19, count: 30, rate: 4 },
    { key: 20, count: 20, rate: 3 },
    { key: 21, count: 40, rate: 2 },
    { key: 22, count: 10, rate: 1 },
]
const categoriesTamplate = (item) => {
    return <p className='gray-text !text-[12px]'>{item?.name}</p>
}
const brandTamplate = (item) => {
    return <p className='gray-text !text-[12px]'>{item?.name}</p>
}
const colorsTamplate = (item) => {
    return (
        <div className='flex items-center'>

            {
                item.color && <div className='w-[20px] h-[20px] rounded-full me-2' style={{ backgroundColor: item?.color }}> </div>
            }

            <p className='gray-text !text-[12px]'>{item?.name}</p>

        </div>
    )
}
const ratingTamplate = item => {
    return (
        <Fragment>

            {
                item.rate
                    ?
                    <Rating rate={item?.rate} isDisabled={true} />
                    :
                    <p className='gray-text !text-[12px]'>{item?.name}</p>
            }

        </Fragment>
    )
}

export {
    categories,
    brands,
    colors,
    ratings,
    categoriesTamplate,
    brandTamplate,
    colorsTamplate,
    ratingTamplate
}