import React, { Fragment, useEffect, useState } from 'react';
import Header from './Header';
import { renderGridProducts, renderMnueProdcusts } from '../SingleProduct/data';
import NotFoundProduct from '../NotFoundProduct';

export default function ProductView({ setShowFilter }) {

    const [layout, setLayout] = useState(localStorage.getItem('system-layout') || 'grid');

    // This Variable To Control Show Of Not Found Product Or Not
    const isNotFoundProduct = false;

    useEffect(() => {

        // set selected layout to localstorage 
        localStorage.setItem('system-layout', layout);

    }, [layout]);

    return (
        <section className='col-span-12 lg:col-span-8 xl:col-span-9'>

            <Header layout={layout} setLayout={setLayout} setShowFilter={setShowFilter} />

            {
                isNotFoundProduct ?
                    <NotFoundProduct />
                    :
                    <Fragment>
                        {
                            layout === "mnue"
                                ?
                                renderMnueProdcusts
                                :
                                <div className='grid grid-cols-12 gap-5 mt-20'>
                                    {renderGridProducts}
                                </div>
                        }

                        <div className='my-10 flex items-center justify-center'>

                            <button className='bg-transparent font-medium p-3 px-20 border border-[var(--light)] rounded-[5px] text-[var(--light)]'>عرض المزيد</button>

                        </div>
                    </Fragment>
            }


        </section>
    )
}
