import React, { useState } from 'react'
import { RadioGroup } from '../../../../components';
import { Slider } from "primereact/slider";
import {
  categories,
  brands,
  colors,
  ratings,
  categoriesTamplate,
  brandTamplate,
  colorsTamplate,
  ratingTamplate
}
  from './data';


export default function ProductsPanel({ classNames }) {

  const [selectedCategories, setSelectedCategories] = useState({ name: 'الكل', key: 1 });

  const [selectedBrand, setSelectedBrand] = useState({ name: 'الكل', key: 1 });

  const [selectedColor, setSelectedColor] = useState({ name: 'الكل', key: 1 });

  const [selectedRate, setSelectedRate] = useState({ name: 'الكل', key: 1 });

  const [minValue, setMinValue] = useState(100);

  const [maxValue, setMaxValue] = useState(5000);

  const [value, setValue] = useState([minValue, maxValue]);

  return (
    <aside className={`${classNames}`} onClick={e => e.stopPropagation()}>

      <div className='border rounded-[5px] p-5'>

        <div>

          <h2 className='font-medium text-[18px] mb-4'>الفئة</h2>

          <RadioGroup items={categories} itemTemplate={categoriesTamplate} selectedItems={selectedCategories} setSelectedItems={setSelectedCategories} />

          <h3 className='font-medium text-[var(--light)] border-b text-[14px] p-2 pb-5 cursor-pointer hover:!underline px-3 mb-4'>عرض المزيد</h3>

        </div>

        <div>

          <h2 className='font-medium text-[18px] mb-4'>الماركة</h2>

          <RadioGroup items={brands} itemTemplate={brandTamplate} selectedItems={selectedBrand} setSelectedItems={setSelectedBrand} />

          <h3 className='font-medium text-[var(--light)] border-b text-[14px] p-2 pb-5 cursor-pointer hover:!underline px-3 mb-4'>عرض المزيد</h3>

        </div>

        <div>

          <h2 className='font-medium text-[18px] mb-4'>السعر</h2>

          <div className='p-2 relative slider-container'>

            <Slider value={value} min={minValue} max={maxValue} onChange={(e) => {

              setValue(e.value);
            }} className="w-[90%] m-auto" range />

            <div className='flex items-center justify-between mt-7'>

              <p className='text-[12px] font-medium'>من</p>

              <p className='text-[12px] font-medium'>الي</p>

            </div>

            <div className='flex items-center justify-between mt-1'>

              <input type='number' value={value[0]} onChange={(e) => {

                setValue([e.target.value, maxValue]);

                setMinValue(e.target.value);

              }} className='w-[48%] rounded-[5px] text-[13px] border-2 p-2' placeholder='من' />

              <input type='number' value={value[1]} onChange={(e) => {

                setValue([minValue, e.target.value]);

                setMaxValue(e.target.value);


              }} className='w-[48%] rounded-[5px] text-[13px] border-2 p-2' placeholder='الي' />


            </div>

          </div>

          <h3 className='font-medium text-[var(--light)] border-b text-[14px] p-2 pb-5 cursor-pointer hover:!underline px-3 mb-4'>عرض المزيد</h3>

        </div>

        <div>

          <h2 className='font-medium text-[18px] mb-4'>اللون</h2>

          <RadioGroup items={colors} itemTemplate={colorsTamplate} selectedItems={selectedColor} setSelectedItems={setSelectedColor} />

          <h3 className='font-medium text-[var(--light)] border-b text-[14px] p-2 pb-5 cursor-pointer hover:!underline px-3 mb-4'>عرض المزيد</h3>

        </div>

        <div>

          <h2 className='font-medium text-[18px] mb-4'>التقييم</h2>

          <RadioGroup items={ratings} itemTemplate={ratingTamplate} selectedItems={selectedRate} setSelectedItems={setSelectedRate} />

          <h3 className='font-medium text-[var(--light)] text-[14px] p-2 pb-5 cursor-pointer hover:!underline px-3 mb-4'>عرض المزيد</h3>

        </div>

        <div className='flex items-center justify-center'>

          <button className='bg-transparent font-medium p-3 px-20 border rounded-[5px]'>إعادة ضبط</button>

        </div>
      </div>



    </aside>
  )
}
