import React, { Fragment, useState } from 'react'
import { ProductsPanel, ProductView } from './components';
import { useEffectControlar } from '../../../assets/hooks';
import { NewsOffers } from '../../components';
import { renderSwiperProdcuts } from './data';


export default function Products() {


    const [showFilter, setShowFilter] = useState(false);

    const { useCloseMnue, useApplyPanelHeightRestriction } = useEffectControlar();


    // Close Filter Mnue If it's Opened When user click on the window
    useCloseMnue(showFilter, setShowFilter);

    useApplyPanelHeightRestriction(showFilter);

    return (
        <Fragment>

            {/* Mobile Filter Mnue */}
            <div onClick={_ => setShowFilter(false)} className={`panel-mnue absolute w-full h-fit transition top-0 z-[10000] ${showFilter ? "opacity-100 right-0 visible" : "opacity-0 right-[-1000px] invisible"}`}>

                {/* To ensure The transition doesn't work with bg when mnue close */}
                <div className={`w-full h-full ${showFilter ? "bg-[#4d4d4d94]" : "bg-transparent"} absolute left-0 top-0 z-[-1]`}></div>

                <div className={`bg-white drop-shadow p-3 w-[300px] sm:w-[350px] `}>

                    <ProductsPanel classNames={'w-full '} isHasaddvertisement={false} />

                </div>

            </div>

            <div className='container !my-10'>

                <div className='grid grid-cols-12 gap-5'>

                    {/* Desktop Filter Mnue */}
                    <ProductsPanel classNames={'hidden lg:block lg:col-span-4 xl:col-span-3 h-fit'} isHasaddvertisement={true} />

                    <ProductView setShowFilter={setShowFilter} />

                </div>

                <NewsOffers items={renderSwiperProdcuts} title={'منتجات مميزة'} classNames={'bg-white'}></NewsOffers>

            </div>

        </Fragment>
    )
}
