import PropTypes from 'prop-types';
import { DropDown } from '../../../../../components';
import { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFilter } from '@fortawesome/free-solid-svg-icons';

export default function Header(props) {

    const { layout, setLayout, setShowFilter } = props;

    const [selectedItem, setSelectedItem] = useState({ name: "من الاعلى الى الاقل", code: 1 });

    return (
        <div className='flex items-center justify-between border rounded-[5px] p-5 mb-10'>

            <div className='flex items-center select-none'>

                <div onClick={_ => setLayout('grid')} className={`cursor-pointer w-[30px] sm:w-[40px] h-[30px] sm:h-[40px] ${layout === 'grid' ? "bg-[var(--light)]" : "bg-[#d6d6d675]"} me-2 p-2 rounded-[5px] flex items-center justify-center border`}>

                    <img src={require('./assests/grid.png')} className='w-full' />

                </div>

                <div onClick={_ => setLayout('mnue')} className={`cursor-pointer w-[30px] sm:w-[40px] h-[30px] sm:h-[40px] ${layout === 'mnue' ? "bg-[var(--light)]" : "bg-[#d6d6d675]"} p-2 rounded-[5px] flex items-center justify-center border`}>

                    <img src={require('./assests/mnue.png')} className='w-full' />

                </div>

                <div onClick={_ => setShowFilter(true)} className={`cursor-pointer w-[30px] sm:w-[40px] h-[30px] sm:h-[40px] bg-[#d6d6d675] mx-2 p-2 rounded-[5px] lg:hidden flex items-center justify-center border`}>

                    <FontAwesomeIcon icon={faFilter} className='text-[#A5A5A5]' />

                </div>

            </div>

            <div className='flex items-center justify-between products-dropdown'>

                <p className='gray-text min-w-[33%]'>ترتيب حسب : </p>

                <DropDown selectedItem={selectedItem} setSelectedItem={setSelectedItem} items={[{ name: "من الاعلى الى الاقل", code: 1 }, { name: "من الاقل الي الاعلي", code: 1 }]} />

            </div>

        </div>
    )
}


Header.propTypes = {
    layout: PropTypes.string,
    setLayout: PropTypes.string,
    setLayout: PropTypes.func,
}